import { useMemo } from 'react'
import { useSelector } from 'react-redux'

import { selectLayers } from '../Keyboard/Layers/layersSlice'
import ResultList from './ResultList'
import style from './style.module.css'
import { pickerPropTypes } from './pickerPropTypes'

export default function LayerPicker ({ onSelect }) {
  const layers = useSelector(selectLayers)
  const choices = useMemo(() => layers.map((layer, i) => ({
    code: i,
    description: layer.name || `Layer ${i}`
  })), [layers])

  return (
    <ResultList
      results={choices}
      onSelect={onSelect}
      renderResult={result => (
        <>
          {result.code}
          {result.description && (
            <p className={style.description}>
              {result.description}
            </p>
          )}
        </>
      )}
    />
  )
}

LayerPicker.propTypes = pickerPropTypes
