import PropTypes from 'prop-types'
import { bindNode } from '../Keyboard/Binding/bindPropTypes'

export const pickerPropTypes = {
  onCancel: PropTypes.func,

  /**
   * For multi-step selections, this just updates the parent component
   */
  onChange: PropTypes.func,

  /**
   * For single-step selections or other events that "complete" the interaction
   * this callback cues the parent to close the picker.
   */
  onSelect: PropTypes.func.isRequired,
  node: bindNode.isRequired,
  searchKey: PropTypes.string.isRequired,
  searchThreshold: PropTypes.number
}
