import fuzzysort from 'fuzzysort'
import { useContext, useMemo } from 'react'

import { SearchContext } from '../providers'
import ResultList from './ResultList'
import SearchFilter from './SearchFilter'
import style from './style.module.css'
import { pickerPropTypes } from './pickerPropTypes'

export default function GenericPicker (props) {
  const { searchKey, node, param, behaviour, context, onSelect } = props
  const { getSearchTargets } = useContext(SearchContext)

  const choices = useMemo(() => {
    return getSearchTargets(param, behaviour.code, context)
  }, [getSearchTargets, param, behaviour, context])

  return (
    <SearchFilter
      choices={choices}
      initialQuery={node.value || ''}
      searchKey={searchKey}
      renderResults={results => results && (
        <ResultList
          results={results}
          onSelect={onSelect}
          renderResult={renderSearchResult}
        />
      )}
    />
  )
}

GenericPicker.propTypes = pickerPropTypes

function renderSearchResult (result) {
  const title = result.search
    ? (
      <span>
        {fuzzysort.highlight(result.search, (v, i) => (
          <b key={i}>{v}</b>
        ))}
      </span>
      )
    : <span>{result.code}</span>

  const description = result.displayName || result.description

  return (
    <>
      {title}
      {description && (
        <p className={style.description}>
          {description}
        </p>
      )}
    </>
  )
}
