import pick from 'lodash/pick'
import PropTypes from 'prop-types'
import { useMemo } from 'react'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import EnumPicker from './EnumPicker'
import { bindNode } from '../Keyboard/Binding/bindPropTypes'

import GenericPicker from './GenericPicker'
import HueSatValPicker from '../EnhancedPickers/RGB/HueSatValPicker'
import KeycodePicker from '../EnhancedPickers/Keycode/KeycodePicker'
import RawValuePicker from './RawValuePicker'
import LayerPicker from './LayerPicker'
import styles from './style.module.css'

export function ValuePicker (props) {
  const { node, type, onSelect, onChange, onConfirm } = props
  const pickerProps = pick(props, [
    'node', 'value', 'prompt', 'param', 'behaviour', 'context', 'searchKey', 'inline'
  ])

  const PickerComponent = useMemo(() => {
    // TODO: allow components to register helpers
    if (type === 'tree' && node.source?.helper === 'hsb-color-picker') return HueSatValPicker
    if (node.slot.raw) return RawValuePicker
    // TODO: Shouldn't need to render placeholder
    if (node.slot.type === 'placeholder') return RawValuePicker
    if (node.slot.enum) return EnumPicker
    if (node.slot === 'code') return KeycodePicker
    if (node.slot === 'layer') return LayerPicker
    return GenericPicker
  }, [node, type])

  return (
    <div className={styles.wrapper}>
      <PickerComponent
        {...pickerProps}
        param={node.slot}
        onSelect={onSelect}
        onChange={onChange}
        onConfirm={onConfirm}
      />
    </div>
  )
}

ValuePicker.propTypes = {
  type: PropTypes.string.isRequired,
  node: bindNode.isRequired,
  behaviour: PropTypes.object.isRequired,
  searchKey: PropTypes.string.isRequired,
  searchThreshold: PropTypes.number,
  inline: PropTypes.bool,
  onCancel: PropTypes.func,
  onUpdate: PropTypes.func,
  onSelect: PropTypes.func.isRequired
}

ValuePicker.defaultProps = {
  type: 'value',
  value: '',
  freeForm: false,
  inline: false,
  searchThreshold: 10
}

export default function ValuePickerDialog (props) {
  const { prompt, onCancel, ...rest } = props

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox dismissText={null} classes={[styles.dialogWrapper]}>
        <h2>{prompt}</h2>
        <ValuePicker {...rest} />
        {/* <div style={{ marginTop: '10px', textAlign: 'center' }}>
          <ActionButton onClick={handleConfirm}>Confirm</ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </div> */}
      </DialogBox>
    </Modal>
  )
}

ValuePickerDialog.propTypes = {
  type: PropTypes.string.isRequired,
  node: bindNode.isRequired,
  behaviour: PropTypes.object.isRequired,
  prompt: PropTypes.string.isRequired,
  searchKey: PropTypes.string.isRequired,
  searchThreshold: PropTypes.number,
  onCancel: PropTypes.func.isRequired,
  onChange: PropTypes.func,
  onSelect: PropTypes.func.isRequired
}
