import { Fragment, useCallback, useMemo } from 'react'
import compact from 'lodash/compact'
import map from 'lodash/map'

import { NullKey, getSourceTitle, renderSource } from '../../Keyboard/Binding/BindValue'
import styles from '../../Keyboard/Keys/styles.module.css'
import { treeToSequence } from './helpers'

export default function KeycodeBindNode ({ node, onSelect, expanded = false }) {
  const handleClick = useCallback(function (event) {
    event.stopPropagation()
    onSelect({ node, type: 'tree' })
  }, [node, onSelect])

  const codes = useMemo(() => map(treeToSequence(node), 'source'), [node])
  const key = useMemo(() => codes.at(-1), [codes])
  const codesSerialized = useMemo(() => {
    const mods = codes.slice(0, -1)
    return mods.reduce((serialized, mod) => (
      `${mod.code}(${serialized})`
    ), key?.code)
  }, [codes, key?.code])
  const codeLabels = useMemo(() => (
    codes.map(renderSource)
      .map((label, i) => <Fragment key={i}>{label}</Fragment>)
      .reduce((labels, label) => (
        labels.length === 0
          ? [label]
          : [...labels, ' ', label]
      ), [])
  ), [codes])
  const title = useMemo(() => (
    codes.map(getSourceTitle).join(' + ')
  ), [codes])

  const display = useMemo(() => (
    codes.map(code => (
      code
        ? code.description || code.name
        : '⦸'
    )).join(' + ')
  ), [codes])

  const missing = (
    node.source?.code === undefined ||
    node.value === undefined ||
    node._isPlaceholder
  )

  const content = !expanded
    ? codeLabels
    : (
      <>
        <code>{codesSerialized}</code>
        <span>{display}</span>
      </>
      )

  return (
    <span
      className={compact([
        styles.code,
        missing && styles.missingValue
      ]).join(' ')}
      title={title}
      onClick={handleClick}
    >
      {missing ? <NullKey /> : content}
    </span>
  )
}
