import { useContext, useMemo } from 'react'

import { SearchContext } from '../providers'
import ResultList from './ResultList'
import style from './style.module.css'
import { pickerPropTypes } from './pickerPropTypes'

export default function EnumPicker (props) {
  const { getSearchTargets } = useContext(SearchContext)
  const { param, onSelect } = props

  const choices = useMemo(() => (
    getSearchTargets(param)
  ), [getSearchTargets, param])

  return (
    <ResultList
      results={choices}
      onSelect={onSelect}
      renderResult={result => (
        <>
          {result.code}
          {result.description && (
            <p className={style.description}>
              {result.description}
            </p>
          )}
        </>
      )}
    />
  )
}

EnumPicker.propTypes = pickerPropTypes
