import compact from 'lodash/compact'
import { useCallback } from 'react'
import PropTypes from 'prop-types'

import * as bindPropTypes from './bindPropTypes'
import styles from '../Keys/styles.module.css'
import Icon from '../../Common/Icon'

export function NullKey () {
  return <span>⦸</span>
}

export function getSourceTitle (source) {
  const description = source?.displayName || source?.description
  return source && `(${source.code}) ${description}`
}

export function renderSource (source) {
  const text = source?.symbol || source?.code?.toString()
  const icon = source?.faIcon && <Icon name={source?.faIcon} />
  return icon || text || <NullKey />
}

export function Source ({ value, expanded = false }) {
  if (!expanded) {
    return renderSource(value)
  } else if (!value) {
    return <NullKey />
  }

  const icon = value.faIcon ? <Icon name={value.faIcon} /> : value.symbol
  const code = value.code?.toString?.()
  const name = value.displayName || value.description || value.name

  // TODO: `name` will be falsey for raw values, maybe a RawValueBindNode would
  // be a more appropriate way to handle them.

  return (
    <>
      <code>{code}</code>
      {name && <span>{name}</span>} {icon}
    </>
  )
}

function BindValue (props) {
  const { node, onSelect, expanded = false } = props
  const { value, source, _isPlaceholder } = node
  const title = getSourceTitle(source)

  const handleClick = useCallback(function (event) {
    event.stopPropagation()
    onSelect({ node })
  }, [node, onSelect])

  return (
    <span
      className={compact([
        styles.code,
        (value === undefined || _isPlaceholder) && styles.missingValue
      ]).join(' ')}
      title={title}
      onClick={handleClick}
    >
      {_isPlaceholder ? <NullKey /> : <Source value={source} expanded={expanded} />}
    </span>
  )
}

BindValue.propTypes = {
  node: bindPropTypes.bindNode.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default BindValue
