import find from 'lodash/find'
import get from 'lodash/get'

import { getBindingSlots } from '../../keymap'

export function makeIndex (tree) {
  const index = []
  ;(function traverse (tree) {
    const params = tree.params || []
    index.push(tree)
    params.forEach(traverse)
  })(tree)

  return index
}

export function isSimple (normalized) {
  const [first] = normalized.params
  const symbol = get(first, 'source.symbol', get(first, 'source.code', ''))
  const hasIcon = !!get(first, 'source.faIcon')
  const shortSymbol = hasIcon || symbol.length === 1
  const singleParam = normalized.params.length === 1
  return singleParam && shortSymbol
}

export function isComplex (normalized, behaviourParams) {
  const [first] = normalized.params
  const symbol = get(first, 'source.symbol', get(first, 'value', ''))
  const hasIcon = !!get(first, 'source.faIcon')
  const isLongSymbol = !hasIcon && symbol.length > 4
  const isMultiParam = behaviourParams.length > 1
  const isNestedParam = get(first, 'params', []).length > 0

  return isLongSymbol || isMultiParam || isNestedParam
}

export function createPromptMessage (param, node, type) {
  const promptMapping = {
    layer: 'Assign layer',
    mod: 'Assign modifier',
    behaviour: 'Assign behavior',
    keycode: 'Assign key code'
  }

  // This currently only applies to &rgb_ug RGB_COLOR_HSB(...) because it is
  // technically a single binding cell in the devicetree and we treat it as
  // multiple parameters.
  if (node) {
    const name = node.source?.displayName || node.source?.name
    if (name) {
      return name
    }
  }

  if (param.name) {
    return `Assign ${param.name}`
  }

  if (param.raw) {
    return 'Enter raw value'
  }

  return (
    promptMapping[param] ||
    promptMapping.keycode
  )
}

export function hasErrors (index) {
  // The first value in the list is the behaviour. If it doesn't have a source
  // that means that we don't have details about what it is.
  if (!index[0].source.code) {
    return true
  }

  for (const value of index) {
    if ((value.source?.params || []).length > value.params.length) {
      return true
    }

    if (value.params.some(v => (
      v.value === undefined ||
      v.value === '' ||
      v.source === undefined ||
      (v._isPlaceholder && v.slot?.type !== 'placeholder')
    ))) {
      return true
    }
  }
}

export function hydrateTree (binding, context) {
  const { value, params } = binding
  const { sources, mergedGlyphs, behaviorParameters } = context

  const bind = value
  const behaviour = get(sources.behaviours, bind, {})
  const behaviourParams = getBindingSlots(binding, behaviorParameters)

  function getSourceValue (value, as) {
    value = get(sources.defines, value, value)
    if (!Number.isNaN(Number(value))) {
      value = Number(value)
    }
    if (as.raw) return { code: value }
    if (as.enum) {
      // TODO: this doesn't seem to handle type mismatches like `1` vs `"1"` but
      // in fairness this has probably been true for some time.
      // TODO: rewrite all behavior enums to wrap choices in objects instead of
      // handling object and raw value formats.
      return (
        find(as.enum, { code: value }) ||
        find(as.enum, value) ||
        { code: value }
      )
    }
    // TODO: Shouldn't need to render placeholder
    if (as.type === 'placeholder') return { code: value }
    return sources?.[as]?.[value]
  }

  function hydrateNode (node, slot) {
    if (!node) {
      return { value: undefined, slot, params: [] }
    }
    const { value, params, _isPlaceholder } = node
    const source = getSourceValue(value, slot)

    return {
      value,
      _isPlaceholder,
      slot,
      source: {
        ...source,
        symbol: get(mergedGlyphs, [source?.name, 'symbol'], source?.symbol),
        faIcon: get(mergedGlyphs, [source?.name, 'faIcon'], source?.faIcon)
      },
      params: get(source, 'params', []).map((slot, i) => (
        hydrateNode(params[i], slot)
      ))
    }
  }

  return {
    value,
    slot: 'behaviour',
    source: behaviour,
    params: behaviourParams.map((slot, i) => (
      hydrateNode(params[i], slot)
    ))
  }
}
