import cloneDeep from 'lodash/cloneDeep'
import { makeIndex } from './util'

/**
 * @typedef {number | string | Object} BindEnumOption
 * @property {string} name
 * @property {string} type
 * @property {string} [description]
 * @property {any} code
*/

/** @typedef {('behaviour' | 'code' | 'layer')} BindTypeBasic */
/** @typedef {{ type: 'placeholder' }} BindTypePlaceholder */
/** @typedef {{ enum: Array<BindEnumOption> }} BindTypeEnum */
/** @typedef {BindTypeBasic | BindTypePlaceholder | BindTypeEnum} BindSlot */
/** @typedef {Array<BindSlot>} BindSlots */

/** @typedef {number | string} BindValue */

/**
 * @typedef {Object} BindSource
 * @property {string} code
 * @property {BindSlots} [params]
 * @property {string} [description]
 * @property {string} [symbol]
 * @property {string} [faIcon]
 */

/**
 * @typedef {Object} BindNode
 * @property {BindValue} value
 * @property {BindParams} params
 * @property {BindSource} source
 * @property {Boolean} [_isPlaceholder=false]
 */

/** @typedef {BindNode} BindTree */

/**
 * @typedef {Object} UpdateSubtreeAction
 * @property {number} nodeIndex
 * @property {BindNode} updatedNode
 */

/**
 * @typedef {Object} UpdateNodeAction
 * @property {number} nodeIndex
 * @property {BindSource} source
 * @property {BehaviorSlots} behaviorParameters
 */

/**
 * Apply a change to a subtree of a bindtree.
 * @param {BindTree} bindTree
 * @param {UpdateSubtreeAction} updateAction
 * @returns {BindTree}
 */
export function bindTreeReducer (bindTree, updateAction) {
  const { nodeIndex, updatedNode } = updateAction
  const updatedTree = cloneDeep(bindTree)
  const index = makeIndex(updatedTree)
  const targetNode = index[nodeIndex]

  // Assume these are all fine?
  targetNode.value = updatedNode.value
  targetNode.params = updatedNode.params
  delete targetNode._isPlaceholder

  index.forEach(node => {
    delete node.source
    delete node.slot
  })

  return updatedTree
}

/**
 * Apply a value from a defined source to bind node
 * @param {BindTree} bindTree
 * @param {UpdateNodeAction} updateAction
 * @returns {BindTree}
 */
export function bindNodeReducer (bindTree, updateAction) {
  const { nodeIndex, source, behaviorParameters } = updateAction
  const updatedTree = cloneDeep(bindTree)
  const index = makeIndex(updatedTree)
  const targetNode = index[nodeIndex]

  // Attempt to cast raw inputs to non-string type if such a type is defined
  // and I've specifically accounted for casting to that type here.
  // No error handling yet, so...
  // TODO: can't RawValuePicker do this??
  if (targetNode.slot.raw && targetNode.slot.type === 'number') {
    source.code = Number(source.code)
  }

  const oldAdditionalParams = targetNode.source?.additionalParams?.length || 0
  const newAdditionalParams = source.additionalParams?.length || 0
  if (newAdditionalParams < oldAdditionalParams) {
    // Originally 6c425092 -- src/Keyboard/Keys/Key.js
    index[0].params.splice(1, index[0].params.length - 1)
    index.splice(2, index.length - 1)
  }

  const newParamSlots = targetNode.slot === 'behaviour'
    ? (behaviorParameters[source.value] || [])
    : (source?.params || [])

  const params = getNewParamsList(targetNode, newParamSlots)

  delete targetNode._isPlaceholder
  targetNode.value = source.code
  targetNode.params = params

  index.forEach(node => {
    delete node.source
    delete node.slot
  })

  return updatedTree
}

/**
 * Resolve parameter type mismatches when changing behaviors
 * @param {BindNode} node
 * @param {BindSlots} newParamSlots
 * @returns {BindParams}
 */
function getNewParamsList (node, newParamSlots) {
  const { params: oldParams, source } = node
  const oldParamSlots = source?.params || []

  // Clear parameters if the new source params list doesn't match the old one.
  // TODO: instead of a reduce and iterating through the entire array in all
  // cases, this could look for the first index where both params lists don't
  // match and then splice targetNode.params from there
  return newParamSlots.reduce((params, slot, i) => [
    ...params,
    // TODO: use `isEqual()`?
    oldParamSlots[i] !== slot
      ? { value: 0, _isPlaceholder: true }
      : oldParams[i]
  ], [])
}
