import without from 'lodash/without'
import { useCallback, useEffect, useState } from 'react'

/**
 * Handle document keyup events, except when focus changed mid-keypress.
 *
 * Mostly this means we can have a handler for something like Ctrl that should
 * not trigger if the user is pressing Ctrl+Tab to switch focus to another
 * browser tab.
 *
 * This probably doesn't work for Cmd+Tab due to how macos gets in the way of
 * handling events for key combinations involving Command.
 *
 * @param {Function} onKeyup
 */
export default function useCancelableKeyPress (onKeyup, options = {}) {
  const { singleKeyOnly = false } = options
  const [, setHeld] = useState([])
  const clear = useCallback(() => setHeld([]), [setHeld])

  useEffect(() => {
    function handleKeyDown (event) {
      setHeld(held => (
        singleKeyOnly ? [event.code] : [...held, event.code]
      ))
    }

    function handleKeyUp (event) {
      setHeld(held => {
        if (held.includes(event.code)) {
          // This is hacky but triggering this within the setState callback
          // means we don't need to use the state as a dependency of the effect
          // and triggering onKeyup asynchronously doesn't seem to be a problem
          // (so far) for handling the event.
          setTimeout(() => onKeyup(event))
        }

        return without(held, event.code)
      })
    }

    document.body.addEventListener('keydown', handleKeyDown)
    document.body.addEventListener('keyup', handleKeyUp)
    document.body.addEventListener('focusout', clear)

    return () => {
      document.body.removeEventListener('keydown', handleKeyDown)
      document.body.removeEventListener('keyup', handleKeyUp)
      document.body.removeEventListener('focusout', clear)
    }
  }, [singleKeyOnly, setHeld, onKeyup, clear])
}
