import classNames from 'classnames'
import without from 'lodash/without'
import PropTypes from 'prop-types'
import { useCallback } from 'react'

import useCancelableKeyPress from '../../Common/hooks/use-cancelable-key-press'
import style from './styles.module.css'

const leftModifiers = ['LS', 'LA', 'LC', 'LG']
const rightModifiers = ['RS', 'RA', 'RC', 'RG']

const modifiersMap = {
  LS: { short: 'LSHFT', full: 'Left Shift' },
  LC: { short: 'LCTRL', full: 'Left Control' },
  LA: { short: 'LALT', full: 'Left Alt' },
  LG: { short: 'LGUI', full: 'Left Gui' },
  RS: { short: 'RSHFT', full: 'Right Shift' },
  RC: { short: 'RCTRL', full: 'Right Control' },
  RA: { short: 'RALT', full: 'Right Alt' },
  RG: { short: 'RGUI', full: 'Right Gui' }
}

function mapDomModifier (event) {
  const modKey = {
    Control: 'C',
    Shift: 'S',
    Alt: 'A',
    Meta: 'G'
  }[event.key]
  const side = {
    [KeyboardEvent.DOM_KEY_LOCATION_LEFT]: 'L',
    [KeyboardEvent.DOM_KEY_LOCATION_RIGHT]: 'R'
  }[event.location]

  return (modKey && side) ? `${side}${modKey}` : null
}

const noDeselectMessage = ' (This modifier is implicitly used by the active keycode and cannot be de-selected)'

function ModifiersRow ({ selected, implicit, row, onChange }) {
  return (
    <ul className={style.modifiers}>
      {row.map((mod, i) => (
        <li
          key={i}
          title={modifiersMap[mod].full + (implicit.includes(mod) ? noDeselectMessage : '')}
          className={classNames({
            [style.selected]: selected.includes(mod),
            [style.implicit]: implicit.includes(mod)
          })}
        >
          <label>
            <input
              type="checkbox"
              checked={(
                selected.includes(mod) ||
                implicit.includes(mod)
              )}
              disabled={implicit.includes(mod)}
              onChange={e => {
                onChange(
                  e.target.checked
                    ? [...selected, mod]
                    : without(selected, mod)
                )
              }}
            /> {(
              modifiersMap[mod].short
            )}
          </label>
        </li>
      ))}
    </ul>
  )
}

export default function ModifiersPicker ({ selected, implicit, onChange }) {
  useModifierToggle(selected, implicit, onChange)

  return (
    <div className={style.allModifiers}>
      <ModifiersRow row={leftModifiers} selected={selected} implicit={implicit} onChange={onChange} />
      <ModifiersRow row={rightModifiers} selected={selected} implicit={implicit} onChange={onChange} />
    </div>
  )
}

ModifiersPicker.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string),
  implicit: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func.isRequired
}

function useModifierToggle (selected, implicit, onChange) {
  const handleKeyUp = useCallback(event => {
    const modifier = mapDomModifier(event)
    if (modifier && !implicit.includes(modifier)) {
      onChange(
        selected.includes(modifier)
          ? without(selected, modifier)
          : [...selected, modifier]
      )
    }
  }, [selected, implicit, onChange])

  useCancelableKeyPress(handleKeyUp, { singleKeyOnly: true })
}
