import { useCallback, useState } from 'react'

import { pickerPropTypes } from './pickerPropTypes'

function focusInput (node) {
  if (node) {
    node.focus()
    node.select()
  }
}

export default function RawValuePicker (props) {
  const { value: initialValue, param, onSelect } = props
  const [value, setValue] = useState(initialValue)

  const handleKeyDown = useCallback(event => {
    if (event.key === 'Enter') {
      onSelect({ code: value })
    }
  }, [value, onSelect])

  const handleKeyPress = useCallback(event => {
    let value = event.target.value

    if (param.type === 'number') {
      value = Number(value)
      if (Number.isNaN(value)) {
        return
      }
    }

    setValue(value)
  }, [param, setValue])

  return (
    <input
      ref={focusInput}
      type="text"
      value={value}
      onChange={handleKeyPress}
      onKeyDown={handleKeyDown}
    />
  )
}

RawValuePicker.propTypes = pickerPropTypes
