import compact from 'lodash/compact'
import PropTypes from 'prop-types'

import useBindingEditor from './binding-editor'

import BindParamlist from './BindParamlist'
import * as bindPropTypes from './bindPropTypes'
import {
  createPromptMessage,
  isSimple,
  isComplex
} from './util'
import styles from '../Keys/styles.module.css'

import Icon from '../../Common/Icon'
import ValuePicker from '../../ValuePicker'

function Binding (props) {
  const { context = 'none', hideBehaviour, value, params, paramIndex, onUpdate } = props
  const [
    { behavior, behaviorParams, bindTree, bindTreeParams, editing, invalid },
    { selectNodeForEditing, clearEditing, handleUpdateAndConfirm, handleChange, handleConfirm }
  ] = useBindingEditor({ value, params }, paramIndex, onUpdate)

  return (
    <>
      {behavior && !hideBehaviour
        ? (
        <span
          className={compact([
            styles['behaviour-binding'],
            !bindTree.source?.code && styles.missingValue
          ]).join(' ')}
          onClick={() => selectNodeForEditing({ node: bindTree })}
        >
          {bindTree.source?.code || '⦸'}
        </span>
          )
        : null}
      {invalid && (
        <Icon
          name="warning"
          className={styles.missing}
          title="Binding has missing value(s)"
        />
      )}
      <BindParamlist
        root={true}
        slots={behaviorParams}
        params={bindTreeParams}
        onSelect={selectNodeForEditing}
        wrapperProps={{
          'data-simple': isSimple(bindTree),
          'data-long': isComplex(bindTree, behaviorParams)
        }}
      />
      {editing && (
        <ValuePicker
          node={editing.node}
          type={editing.type}
          behaviour={behavior}
          context={context}
          prompt={createPromptMessage(editing.node.slot)}
          searchKey="code"
          onSelect={handleUpdateAndConfirm}
          onConfirm={handleConfirm}
          onChange={handleChange}
          onCancel={clearEditing}
        />
      )}
    </>
  )
}

Binding.propTypes = {
  context: PropTypes.string,
  hideBehaviour: PropTypes.bool.isRequired,
  value: bindPropTypes.value.isRequired,
  params: PropTypes.arrayOf(bindPropTypes.node),
  onUpdate: PropTypes.func.isRequired
}

Binding.defaultProps = {
  hideBehaviour: false
}

export default Binding
