import { useCallback, useMemo } from 'react'
import { HslColorPicker } from 'react-colorful'
import ActionButton from '../../Common/ActionButton'

import { pickerPropTypes } from '../../ValuePicker/pickerPropTypes'

export default function HSBPicker (props) {
  const { node, onChange, onConfirm, inline } = props
  const hsl = useMemo(() => ({
    h: Number(node.params[0].value),
    s: Number(node.params[1].value),
    l: Number(node.params[2].value)
  }), [node.params])

  const handleChange = useCallback(({ h, s, l }) => {
    onChange({
      ...node,
      params: [
        { value: Math.round(h) },
        { value: Math.round(s) },
        { value: Math.round(l) }
      ]
    })
  }, [node, onChange])

  return (
    <>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <HslColorPicker
          color={hsl}
          onChange={handleChange}
        />
      </div>
      {!inline && (
        <p style={{ textAlign: 'center' }}>
          <ActionButton onClick={onConfirm}>Confirm</ActionButton>
        </p>
      )}
    </>
  )
}

HSBPicker.propTypes = pickerPropTypes
